import React from 'react';
import { Button } from 'reactstrap';
import FeatherIcon from 'feather-icons-react';

const SimplePDFViewer = ({ 
  pdfPath, 
  title = "Documento PDF"
}) => {

  const downloadPDF = () => {
    const link = document.createElement('a');
    link.href = pdfPath;
    link.download = title.replace(/\s+/g, '_') + '.pdf';
    link.target = '_blank';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const openPDFInNewTab = () => {
    window.open(pdfPath, '_blank');
  };

  return (
    <div className="simple-pdf-viewer text-center p-4 border rounded">
      <div className="mb-3">
        <FeatherIcon icon="file-text" size={48} className="text-primary" />
      </div>
      
      <h6 className="mb-3">{title}</h6>
      
      <div className="d-flex justify-content-center gap-2">
        <Button color="primary" size="sm" onClick={openPDFInNewTab}>
          <FeatherIcon icon="external-link" size={14} className="me-1" />
          Abrir
        </Button>
        <Button color="success" size="sm" onClick={downloadPDF}>
          <FeatherIcon icon="download" size={14} className="me-1" />
          Descargar
        </Button>
      </div>
    </div>
  );
};

export default SimplePDFViewer;